// initialize base framework
import Vue from 'vue'
// vuex store
import store from './store'
// vue router
import {router} from './router'
import './router/permission'
// import app
import App from './App'
// load custom theme
import { loadCurrentTheme } from '@/utils/theme'
// Element UI module & style
import ElementUI from 'element-ui'
import VueAMap from 'vue-amap'
// custom zhx-vue-plateform icons
import '@/assets/plateform-font/iconfont.css'
/**
 * custom vue plugins
 */
// contextmenu
import contextmenu from './components/contextmenu'
// nextPage
// import NextPage from './components/nextPage/'
// breadcrumb
import breadcrumb from './components/breadcrumb'
import Print from 'vue-print-nb'
Vue.use(Print)
// config to use Staic router or dynamic one
Vue.useStaticRouter = false
// initialize custom theme
loadCurrentTheme()
// apply plugins
Vue.use(ElementUI, { size: 'mini' })
// Vue.use(NextPage, { router, store })
Vue.use(contextmenu)
Vue.use(breadcrumb)

Vue.config.productionTip = false

Vue.use(VueAMap)
VueAMap.initAMapApiLoader({
  key: '8703164b6d9a53e64195238e3e17b1f3',
  plugin: ['AMap.RangingTool', 'AMap.MouseTool', 'AMap.PolyEditor', 'AMap.CircleEditor', 'AMap.Autocomplete', 'AMap.EllipseEditor', 'AMap.RectangleEditor', 'AMap.PlaceSearch', 'AMap.Scale', 'AMap.OverView', 'AMap.ToolBar', 'AMap.MapType', 'AMap.PolyEditor', 'AMap.CircleEditor', 'AMap.Geocoder', 'Geolocation'],
  uiVersion: '1.0'
})

Vue.prototype.echartsResize = function(ref){
  setInterval(() => {
    window.onresize = function () {
      ref.resize()
    }
  }, 100)
}


/* eslint-disable no-new */
new Vue({
  el: '#app',
  store,
  router,
  components: { App },
  template: '<App/>'
})



