import Layout from '@/views/Layout'

const _import_ = file => () => import('@/views/' + file + '.vue')
/**
 * 静态路由表配置
 *
 * 可根据{role}字段来模拟权限访问. 用户登录后必须含有{role}字段, 与之匹配的路由会被最终过滤出来供访问.
 * 不匹配的则不会包含在内.
 * 具体字段表说明可以访问平台文档查看详细资料.
 */
export const aysncRoutesMap = [
  {
    path: '/device-state',
    component: Layout,
    meta: { title: '设备状态', role: ['admin', 'user'] },
    children: [
      {
        path: '',
        name: 'DeviceState',
        component: _import_('DeviceState/index'),
        meta: { title: '设备状态', icon: 'fdddfont icon-shebei' }
      }
    ]
  },
  {
    path: '/statistical-query',
    component: Layout,
    meta: { title: '统计查询', role: ['admin', 'user'] },
    children: [
      {
        path: '',
        name: 'StatisticalQuery',
        component: _import_('StatisticalQuery/index'),
        meta: { title: '统计查询', icon: 'fdddfont icon-tubiao' }
      }
    ]
  },
  {
    path: '/basic-information',
    component: Layout,
    meta: { title: '基础信息', icon: 'fdddfont icon-jichushujuguanli', role: ['admin', 'user'] },
    children: [
      {
        path: 'organization-management',
        name: 'OrganizationManagement',
        component: _import_('BasicInformation/Organization/index'),
        meta: { title: '组织机构', icon: 'fdddfont icon-msnui-org' }
      },
      {
        path: 'line-management',
        name: 'LineManagement',
        component: _import_('BasicInformation/Line/index'),
        meta: { title: '线路管理', icon: 'fdddfont icon-xianlu' }
      },
      {
        path: 'bus-management',
        name: 'BusManagement',
        component: _import_('BasicInformation/Bus/index'),
        meta: { title: '车辆管理', icon: 'fdddfont icon-gongjiao' }
      },
      {
        path: 'driver-management',
        name: 'DriverManagement',
        component: _import_('BasicInformation/Driver/index'),
        meta: { title: '司机管理', icon: 'fdddfont icon-siji-' }
      },
      {
        path: 'device-management',
        name: 'DeviceManagement',
        component: _import_('BasicInformation/Device/index'),
        meta: { title: '设备管理', icon: 'fdddfont icon-icon-' }
      },
      {
        path: 'operating-information',
        name: 'OperatingInformation',
        component: _import_('BasicInformation/OperatingInfo/index'),
        meta: { title: '运营信息', icon: 'fdddfont icon-page' }
      }
    ]
  },
  {
    path: '/dev-information',
    component: Layout,
    meta: { title: '招商管理', icon: 'fdddfont icon-jichushujuguanli', role: ['admin', 'user'] },
    children: [
      {
        path: 'devp',
        name: 'Devp',
        component: _import_('Development/Contract/index'),
        meta: { title: '意向合同', icon: 'fdddfont icon-msnui-org' }
      },
      {
        path: 'examine',
        name: 'Examine',
        component: _import_('Development/Examine/index'),
        meta: { title: '合同审批', icon: 'fdddfont icon-msnui-org' }
      },
      {
        path: 'product-template',
        name: 'ProductTemplate',
        component: _import_('Development/ProductTemplate/index'),
        meta: { title: '产品模版管理', icon: 'fdddfont icon-page' }
      }
    ]
  },
  {
    path: '/warehouse-management',
    component: Layout,
    meta: { title: '仓储管理', icon: 'fdddfont icon-jichushujuguanli', role: ['admin', 'user'] },
    children: [
      {
        path: 'taskPool',
        name: 'TaskPool',
        component: _import_('Development/WarehouseManagement/TaskPool/index'),
        meta: { title: '任务池', icon: 'el-icon-star-off' }
      },
      {
        path: 'entry',
        name: 'Entry',
        component: _import_('Development/WarehouseManagement/Entry/index'),
        meta: { title: '收货单管理', icon: 'el-icon-document' }
      },
      {
        path: 'delivery',
        name: 'Delivery',
        component: _import_('Development/WarehouseManagement/Delivery/index'),
        meta: { title: '出库单管理', icon: 'el-icon-document' }
      },
      {
        path: 'warehouse',
        name: 'Warehouse',
        component: _import_('Development/WarehouseManagement/Warehouse/index'),
        meta: { title: '仓库管理', icon: 'el-icon-news' }
      },
      {
        path: 'shelves',
        name: 'Shelves',
        component: _import_('Development/WarehouseManagement/Shelves/index'),
        meta: { title: '库位管理', icon: 'el-icon-goods' }
      },
      {
        path: 'quality',
        name: 'Quality',
        component: _import_('Development/WarehouseManagement/Quality/index'),
        meta: { title: '质检管理', icon: 'fdddfont icon-msnui-data-supervise' }
      },
      {
        path: 'maintenance',
        name: 'Maintenance',
        component: _import_('Development/WarehouseManagement/Maintenance/index'),
        meta: { title: '维修管理', icon: 'fdddfont icon-tools' }
      },
      {
        path: 'goods',
        name: 'Goods',
        component: _import_('Development/WarehouseManagement/Goods/index'),
        meta: { title: '物件管理', icon: 'el-icon-sold-out' }
      },
      {
        path: 'stockCheck',
        name: 'StockCheck',
        component: _import_('Development/WarehouseManagement/stockCheck/index'),
        meta: { title: '库存盘点', icon: 'fdddfont icon-tools' }
      },
      {
        path: 'stockManagement',
        name: 'StockManagement',
        component: _import_('Development/WarehouseManagement/stockManagement/index'),
        meta: { title: '库存管理', icon: 'fdddfont icon-tools' }
      },{
        path: 'transactionDetail',
        name: 'TransactionDetail',
        component: _import_('Development/WarehouseManagement/transactionDetail/index'),
        meta: { title: '交易明细', icon: 'fdddfont icon-tools' }
      },{
        path: '/statistics',
        component: _import_('Development/WarehouseManagement/statistics/index'),
        meta: { title: '数据报表', icon: 'fdddfont icon-tools' },
        children:[
          {
            path: 'demo3D',
            name: 'Demo3D',
            component: _import_('Development/WarehouseManagement/statistics/demo3D/index'),
            meta: { title: '3D效果demo', icon: 'fdddfont icon-personstalker' }
          }
        ]
      },
    ]
  },
  {
    path: '/data-report',
    component: Layout,
    meta: { title: '数据报表', icon: 'el-icon-tickets', role: ['admin'] },
    children: [
      {
        path: 'regular-data',
        name: 'RegularData',
        component: _import_('Development/DataReport/Regular/index'),
        meta: { title: '常规数据统计', icon: 'el-icon-document' }
      },
      {
        path: 'shelve-data',
        name: 'ShelveData',
        component: _import_('Development/DataReport/Shelve/index'),
        meta: { title: '上架数据统计', icon: 'el-icon-document' }
      },
      {
        path: 'order-data',
        name: 'OrderData',
        component: _import_('Development/DataReport/Order/index'),
        meta: { title: '出入库数据统计', icon: 'el-icon-document' }
      },
      {
        path: 'quality-data',
        name: 'QualityData',
        component: _import_('Development/DataReport/Quality/index'),
        meta: { title: '质检数据统计', icon: 'el-icon-document' }
      },
      {
        path: 'operation-detail',
        name: 'OperationDetail',
        component: _import_('Development/DataReport/OperationDetail/index'),
        meta: { title: '库工操作明细表', icon: 'fdddfont icon-tools' }
      }
    ]
  },
  {
    path: '/system-management',
    component: Layout,
    meta: { title: '系统管理', icon: 'fdddfont icon-setting', role: ['admin'] },
    children: [
      // {
      //   path: 'system-detection',
      //   name: 'SystemDetection',
      //   component: _import_('SystemManagement/SystemDetection/index'),
      //   meta: { title: '系统监测', icon: 'fdddfont icon-msnui-data-supervise' }
      // },
      {
        path: 'user-management',
        name: 'UserManagement',
        component: _import_('SystemManagement/User/index'),
        meta: { title: '用户管理', icon: 'fdddfont icon-personstalker' }
      },
      {
        path: 'role-management',
        name: 'RoleManagement',
        component: _import_('SystemManagement/Role/index'),
        meta: { title: '角色管理', icon: 'fdddfont icon-user-group' }
      },
      {
        path: 'menu-management',
        name: 'MenuManagement',
        component: _import_('SystemManagement/Menu/index'),
        meta: { title: '菜单管理', icon: 'fdddfont icon-menu3caidan3' }
      },
      {
        path: 'log-management',
        name: 'LogManagement',
        component: _import_('SystemManagement/Log/index'),
        meta: { title: '日志管理', icon: 'fdddfont icon-rizhi' }
      },
      {
        path: '/system-management/system-params',
        name: 'SystemParams',
        component: _import_('SystemManagement/SystemParams/index'),
        meta: { title: '系统参数', icon: 'fdddfont icon-canshu1', role: ['admin'] },
        children: [
          // {
          //   path: 'authenticate-configuration',
          //   name: 'AuthConf',
          //   component: _import_('SystemManagement/SystemParams/AuthConf/index'),
          //   meta: { title: '认证配置', icon: 'fdddfont icon-sijiguanli' }
          // },
          {
            path: 'dictionary-management',
            name: 'DictManagement',
            component: _import_('SystemManagement/SystemParams/dict/index'),
            meta: { title: '字典管理', icon: 'fdddfont icon-zidian' }
          },
          {
            path: 'dictionary-configuration',
            name: 'DictConf',
            component: _import_('SystemManagement/SystemParams/DictConf/index'),
            meta: { title: '字典维护', icon: 'fdddfont icon-zidian' }
          },
          // {
          //   path: 'interface-configuration',
          //   name: 'InterfaceConf',
          //   component: _import_('SystemManagement/SystemParams/InterfaceConf/index'),
          //   meta: { title: '接口配置', icon: 'fdddfont icon-jiekouguanli' }
          // },
          {
            path: 'params-configuration',
            name: 'ParamsConf',
            component: _import_('SystemManagement/SystemParams/ParamsConf/index'),
            meta: { title: '参数配置', icon: 'fdddfont icon-navicon-ywcs' }
          }
        ]
      }
    ]
  }
]



